import supportedExportFormats, { SEPA_DD_TYPE } from './constants';

const helpers = ({ $filter, stateContainer }) => {
  const i18n = $filter('i18n');
  const isProductSelected = $ctrl => !!$ctrl.product && $ctrl.product.productId !== -1;

  const getTransactions = stateContainer.createSelector(state => state.transactions.rawData);
  const getSearchTransactions = stateContainer.createSelector(state => state.search.rawData);
  const getSearchingState = stateContainer.createSelector(state => state.searching);
  const getLoadingState =
    stateContainer.createSelector(state => state.transactions.loading || state.search.loading);

  /**
   *
   * @description
   * Checks if transactions data is available
   * @inner
   *
   * @name initialTransactionsAvailable
   * @type {function}
   * @returns {boolean} returns true if transactions data is not present
   */
  const initialTransactionsAvailable = () =>
    (!getSearchingState() && Array.isArray(getTransactions()) && getTransactions().length);

  /**
   * @description
   * Checks if searched transactions data is available
   * @inner
   *
   * @name searchTransactionsAvailable
   * @type {function}
   * @returns {boolean} returns true if searched transactions data is not present
   */
  const searchTransactionsAvailable = () => (
    getSearchingState() &&
    Array.isArray(getSearchTransactions()) &&
    getSearchTransactions().length
  );

  return {
    /**
     * @description
     * Configuration for table headers
     *
     * @name headers
     * @type {array.<Header>}
     *
     */
    headers: [
      {
        label: 'details.label.product',
        class: '',
        isHidden: $ctrl => isProductSelected($ctrl),
        sortable: {
          key: 'productName',
          direction: 'ASC',
        },
      },
      {
        label: 'details.label.counterParty',
        class: '',
        isHidden: () => false,
        sortable: {
          key: 'counterPartyName',
          direction: 'ASC',
        },
      },
      {
        label: 'details.label.description',
        class: '',
        isHidden: () => false,
        sortable: {
          key: 'description',
          direction: 'ASC',
        },
      },
      {
        label: 'details.label.currency',
        class: 'text-right',
        isHidden: () => false,
        sortable: {
          key: 'currency',
          direction: 'ASC',
        },
      },
      {
        label: 'details.label.amount',
        class: 'text-right',
        isHidden: () => false,
        sortable: {
          key: 'amount',
          direction: 'ASC',
        },
      },
      {
        label: 'details.label.bookingDate',
        class: 'text-right',
        sortable: {
          key: 'bookingDate',
          direction: 'DESC',
        },
      },
    ],

    /**
     * @description
     * Checks if sortable object is the active one
     *
     * @name isSortableActive
     * @type {function}
     * @see headers
     *
     * @param {object}  sortable  Sortable object to check
     * @param {object}  sort      Current sort status
     *
     * @returns {boolean}
     */
    isSortableActive: (sortable, sort) =>
      sortable.key === sort.orderBy,

    /**
     * @description
     * Opens the modal window with selectedTransaction
     * @name openModalWindow
     * @type {function}
     * @param {object} ext extension reference
     * @param {object} selectedTransaction   transaction to be shown
     */
    openModalWindow: (ext, selectedTransaction) => {
      Object.assign(ext, { showModal: true, transaction: selectedTransaction });
    },

    /**
     * @description
     * Toggle row details visibility
     * @name closeModalWindow
     * @type {function}
     * @param {object} ext extension reference
     */
    closeModalWindow: (ext) => {
      Object.assign(ext, { showModal: false, transaction: null });
    },

    /**
     * @description
     * Check if value is defined, not null and not empty
     * @name hasValue
     * @type {function}
     * @param {*} value  The value to check
     * @return {boolean}
     */
    hasValue: value => !!value,

    /**
     * @description
     * Check if transaction type is SEPA DD
     * @name isSepaDD
     * @type {function}
     * @param {string} type  Transaction type to check
     * @return {boolean}
     */
    isSepaDD: type => type === SEPA_DD_TYPE,

    /**
     * @description
     * List of supported document export formats
     *
     * @public
     * @name exportFormats
     * @type {function}
     *
     * @return {array}
     */
    exportFormats: () => supportedExportFormats,

    /**
     * @description
     * Checks if actual pagination type matches the one, defined in properties
     *
     * @public
     * @name isPaginationTypeMatch
     * @type {function}
     * @param {function} $ctrl      Current controller
     * @param {string} type         Description of pagination type (pagination, load-more)
     *
     * @returns {boolean}
     */
    isPaginationTypeMatch: ($ctrl, type) =>
      $ctrl.state.pageParams.paginationType === type,

    /**
     * @name isProductSelected
     * @type {function}
     * @param {object} $ctrl  Current controller
     *
     * @description
     * Returns boolean true if product is selected, false - otherwise
     *
     * @returns {boolean}
     */
    isProductSelected,

    /**
     * @description
     * Get the correct translated empty message when product is selected or not
     *
     * @name getEmptyMessage
     * @type {function}
     * @param {object} $ctrl  Current controller
     *
     * @returns {string}
     */
    getEmptyMessage: $ctrl => {
      const selectedMessage = 'message.no.transactions.subtitle';
      const notSelectedMessage = 'message.noTransactionsForAccount';

      return i18n(isProductSelected($ctrl) ? selectedMessage : notSelectedMessage);
    },

    /**
     * @description
     * Checks if transactions data is available
     *
     * @name isTransactionsAvailable
     * @type {function}
     * @returns {boolean} returns true if transactions data is not present
     */
    isTransactionsAvailable: () =>
      (initialTransactionsAvailable() || searchTransactionsAvailable()),

    /**
     * @description
     * Checks if transactions are in a loading state
     *
     * @name isLoading
     * @type {function}
     * @returns {boolean} returns true if transactions data is loading
     */
    isLoading: () => getLoadingState(),
  };
};

export default helpers;

/**
 * Header configuration object
 * @typedef {object} Header
 * @property {string} label       Translation key
 * @property {string} class       CSS class name
 * @property {function} isHidden  Check if header is hidden
 * @property {Sortable} sortable  Sortable configuration
 */

 /**
 * Sortable configuration object
 * @typedef {object} Sortable
 * @property {string} key       Defined sort key in the model
 * @property {string} direction Current sort direction (ASC, DESC)
 */



